#dropdowns-outer {
// 	position: absolute; 
// 	right: 21px;
// 	top: 31px;
	width: 260px;
	height: 33px;
	overflow: hidden;
	z-index: 1000;
	float: right; 
}
#findbar, #gotolinebar {
	position: relative;
	left: 0;
	width: 100%;
	height: 32px;
	background-image: -webkit-gradient(
		linear,
		left top,
		left bottom,
		color-stop(0, #cac6bf),
		color-stop(1, #b1aea7)
	);
	border: 1px solid #d2d0cc;
	border-radius: 0 0 5px 5px;
	-webkit-box-shadow: 0 1px 0 rgba(80,66,57,0.68);
	z-index: 1000;
	-webkit-box-sizing: border-box; 
	box-sizing: border-box;	
}
#gotolinebar {
	width: 50%;
	left: 50%;
}
#findbar input, #gotolinebar input {
    border: 1px solid hsl(44, 5%, 60%);
    border-radius: 2px;
    -webkit-box-shadow: inset 0 2px 0 hsla(0, 0%, 89%, 0.5);
    position: absolute;
    top: 4px;
    left: 5px;
    height: 24px;
    width: 178px;
    z-index: 2;
    font-size: 13px;
}
#gotolinebar input {
	width: 70px;
}
#findbar button, #gotolinebar button {
	width: 25px;
	height: 24px;
	position: absolute;
	right: 28px;
	top: 4px;
	z-index: 1;
}
#findbar .up, #findbar .down {
	background: rgba(227,227,227,0.1);
	line-height: 28px;
}
#findbar .up {
	right: 53px;
}
#findbar .down {
	background-position: -25px 0;
}
#gotolinebar .go {
	background: rgba(227,227,227,0.1) url(../img/skin/go.png);
}
#findbar button:hover, #gotolinebar button:hover { background-color: rgba(227,227,227,0.25); }
#findbar button:active, #gotolinebar button:active { background-color: rgba(150,150,150,0.1); }
#findbar .close, #gotolinebar .close {
	right: 7px;
	top: 0px;
	color: white;
	position: absolute;
	&:before {
	    color: white;
	}
}